/* filters */
.filter {
  --brightness: 1;
  --contrast: 1;
  --saturate: 1;
  --blur: 0px;
  --drop-shadow: 0px 0px 0px rgba(0, 0, 0, 0.0);
  filter: brightness(var(--brightness)) contrast(var(--contrast)) saturate(var(--saturate)) blur(var(--blur)) drop-shadow(var(--drop-shadow));
