/* svg */
.fill-current {
  fill: currentColor;
}

.stroke-current {
  stroke: currentColor;
}

.stroke-0 {
  stroke-width: 0;
}

.stroke-1 {
  stroke-width: 1;
}

.stroke-2 {
  stroke-width: 2;
}

.stroke-butt {
  stroke-linecap: butt;
}

.stroke-round {
  stroke-linecap: round;
}

.stroke-square {
  stroke-linecap: square;
}

/* tables */
.border-collapse {
  border-collapse: collapse;
}

.border-separate {
  border-separate: separate;
}

.table-auto {
  table-layout: auto;
}

.table-fixed {
  table-layout: fixed;
}

/* theme */
@media (prefers-color-scheme: dark) {
  html[theme="auto"] {
    filter: invert(1) hue-rotate(180deg);
  }
  img[theme="auto"] {
    filter: invert(1) hue-rotate(180deg);
  }
  [theme="auto"] {
    filter: invert(1) hue-rotate(180deg);
  }
}

@media (prefers-color-scheme: light) {
  html[theme="auto"] {
    filter: invert(0) hue-rotate(0deg);
